import React from 'react'
import styled from 'styled-components'
import img1 from '../images/about-gallery/img1.jpg'
import img2 from '../images/about-gallery/img2.jpg'
import img3 from '../images/about-gallery/img3.jpg'
import img4 from '../images/about-gallery/img4.jpg'
import img5 from '../images/about-gallery/img5.jpg'
import img6 from '../images/about-gallery/img6.jpg'
import img7 from '../images/about-gallery/img7.jpg'
import img8 from '../images/about-gallery/img8.jpg'
import img9 from '../images/about-gallery/img9.jpg'
import img10 from '../images/about-gallery/img10.jpg'
import img11 from '../images/about-gallery/img11.jpg'
import img12 from '../images/about-gallery/img12.jpg'
import img13 from '../images/about-gallery/img13.jpg'
import img14 from '../images/about-gallery/img14.jpg'
import img15 from '../images/about-gallery/img15.jpg'
import img16 from '../images/about-gallery/img16.jpg'
import img17 from '../images/about-gallery/img17.jpg'
import img18 from '../images/about-gallery/img18.jpg'
import img19 from '../images/about-gallery/img19.jpg'
import { useState } from 'react'
import { useRef } from 'react'
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';

export default function AboutGallery() {
    const [image, setImage] = useState(null)
    const [currentImage, setCurrentImage] = useState(0)
    const gridRef = useRef()
    const modalRef = useRef()

    let list =[img1, img2, img3, img4, img5, img6, img7, img8, img9, img10, img11, img12, img13, img14, img15, img16, img17, img18, img19]

    const openModal = (img) => {
        if (window.innerWidth < 950) {
            return
        }
        if(window.innerWidth < 1200) {
            window.scrollTo(0, 1410)
            if (window.innerWidth < 1020) {
                window.scrollTo(0, 1470)
                if (window.innerWidth < 990) {
                    window.scrollTo(0, 1510)
                }
            } 
        }  else {
            window.scrollTo(0, 1420)
        }
        setImage(img)
        document.body.style.overflowY = 'hidden'
    }
    const closeModal = e => {
        if (e.target.id === "modal") {
            setImage(null)
            document.body.style.overflowY = 'scroll'
        }
    }

    

    const handleClick = (dir, image) => {
        let index = list.indexOf(image)
        dir === "left" ? setImage(index > 0 ? list[index - 1] : list[list.length - 1]) :
            setImage(index < list.length - 1 ? list[index + 1] : list[0])

    }

    return (
        <Container ref={gridRef}>
            {
                list && list.map((img, i) => (
                    <ImgWrapper key={i}>
                        <img src={img} onClick={() => openModal(img)}/>
                    </ImgWrapper>
                ))
            }

            {image && <Modal ref={modalRef} id="modal" onClick={e => closeModal(e)}>
                <ArrowBackIosIcon onClick={() => handleClick('left', image)}/>
                    <img src={image}  />
                <ArrowForwardIosIcon onClick={() => handleClick('right', image)}/>
            </Modal>}
        </Container>
    )
}



const Container = styled.aside`
    margin-top: 200px;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(270px, 1fr));
    grid-auto-rows: 270px;
    gap: 20px;
    position: relative;
`
const ImgWrapper = styled.div`
    background-color: #000000d6;
    overflow: hidden;
    >img {
        transition: transform .5s linear;
        opacity: 0.7;
        width: 100%;
        height: 100%;
        object-fit: contain;
        cursor: pointer;
        &:hover {
            transform: scale(1.05);
            opacity: 1;
        }
    }
`
const Modal = styled.div`
    background-color: #000000b3;
    position: absolute;
    top: -5vh;
    left: -5vw;
    z-index: 100;
    width: 100vw;
    height: 115vh;
    display: flex;
    align-items: center;
    justify-content: space-between;
    >svg {
        color: #a8a8a8;
        font-size: 2rem;
        cursor: pointer;
        margin: 30px;
        &:hover {
            color: #222222;
        }
    }
    >img {
        max-width: 80vw;
        max-height: 80vh;
        object-fit: contain;
    }
`
